@import '../common/style/index.less';

.@{prefix}-stepper {
  display: flex;
  align-items: center;

  &.@{prefix}-stepper--normal {
    .@{prefix}-stepper__input {
      display: block;
      font-size: 16px;
      color: #000;
      text-align: center;
      border: 0;
      vertical-align: top;
      background: 0;
      height: 48rpx;
    }

    .@{prefix}-stepper__minus,
    .@{prefix}-stepper__plus {
      display: block;
      box-sizing: border-box;
      height: 48rpx;
      padding-top: 5rpx;
    }
  }

  // 简洁模式样式
  &.@{prefix}-stepper--pure {
    .@{prefix}-stepper__input {
      border-radius: 2px;
      height: 48rpx;
      margin: 0 5px;
      background-color: #f1f2f5;
      text-align: center;
      font-size: 24rpx;
    }

    .@{prefix}-stepper__minus,
    .@{prefix}-stepper__plus {
      box-sizing: border-box;
      border-radius: 2px;
      background-color: #f1f2f5;
      padding: 5rpx 10rpx;
      height: 48rpx;
    }
  }

  .@{prefix}-is-disabled{
    .@{prefix}-stepper__input {
      color: rgba(0, 0, 0, 0.26);
    }
  }
}
